import React, { useState, useEffect } from 'react';
import "../css/boutonscroll.css"; // importation css Button scroll
import Arrow from "../assets/arrow.png"; // importation emoticone complmentaire

function Bouttonscroll() { // Component Button to scroll

  const [isVisible, setIsVisible] = useState(false); // Etat visibilit button (stock)

  useEffect(() => {
    window.addEventListener('scroll', toggleVisibility); // ecouteur d'venement au dfilement sur la fentre  du haut vers le bas
    // togglevisibility est appel lors du dclenchement
    return () => {
      window.removeEventListener('scroll', toggleVisibility); // togglevisibility est enlev lors du scroll invers bas vers le haut 
    };
  }, []);

  const toggleVisibility = () => {
    if (window.scrollY > 300) { // Si la position de dfilement est suprieure  300 pixels depuis le haut
      setIsVisible(true);
    } else {
      setIsVisible(false); // Sinon, dfinit isVisible sur false, indiquant que le bouton doit tre masqu
    }
  };

  const scrollToTop = () => {  // Utilise la mthode scrollTotop  pour faire dfiler la fentre vers le haut de la page

    window.scrollTo({
      top: 0, // position haut de la page
      behavior: 'smooth' // dfilement fluide pour amliorer l'ux
    });
  };

  return (
    <div className="scroll-to-top">
      {isVisible && (
        <button onClick={scrollToTop}> {/* Bouton pour revenir vers le haut de la page */}
        <img src={Arrow} alt="arrow" className="image" width="25px" height="25px" />

        </button>
      )}
    </div>
  );
}

export default Bouttonscroll;
