import React, { useState } from 'react';
import '../../css/faq.css'; // importation Fichier .css faq
import { Link } from 'react-router-dom';

export default function FAQ() { // Component Section FAQ
  const [formData, setFormData] = useState({ /*Eat formulaire donnes (stock)*/ 
    firstName: '', // prnom
    lastName: '', // nom
    email: '',
    message: ''
  });

  
const [isSubmitted, setIsSubmitted] = useState(false);/*Eat envoir formulaire donnes (stock)*/ 


  const handleChange = (e) => {// Fonction pour cibler les donnes formulaire avec l'evenement e
    const { name, value } = e.target;
    setFormData({ // mise  jour de l'tat de donne du formulaire
      ...formData,
      [name]: value
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Les donnes du formulaire sont disponibles dans l'tat formData
    console.log('Donnes du formulaire :', formData);
    // Ajoutez ici le code pour traiter le formulaire, par exemple, l'envoyer  un serveur
    setIsSubmitted(true);
    console.log('envoi ok'); 
  };

  const reset = () => { // fonction reinitialiser 
    setFormData({// mise  jour de l'tat de donne du formulaire
      firstName: '',
      lastName: '',
      email: '',
      message: ''
    });
    console.log('reset ok'); 
  setIsSubmitted(false); // Rinitialisation de la variable isSubmitted  false
 
  };
  return (
    <>
     <div className="containerinfos">
        <div className='childcontainer'>
          <h1 id='titlefaq'>Bienvenue dans notre page FAQ . Des questions  ce stade?</h1><br/>
          <h1 id='soustitlefaq'>Vous trouverez ci-dessous les rponses aux questions les plus frquemment poses. Si vous ne trouvez pas la rponse  votre question, n'hsitez pas  nous contacter.</h1>
          <div class="box1">
            <h2 id='billeterie'>Billeterie</h2>
            <h3>1. Comment acheter des billets pour le festival ?</h3>
            <p className='question1'>Pour acheter des billets pour le festival, vous pouvez vous rendre sur notre site web officiel et suivre les consignes pour effectuer votre achat en ligne. Les billets peuvent galement tre disponibles dans certains points de vente physiques. N'hsitez pas  consulter notre site web pour plus d'informations</p>
            <h3>2. Puis-je acheter des billets sur place ?</h3>
            <p className='question2'>Oui, des billets pour le festival seront disponibles  l'achat sur place, sous rserve de disponibilit. Cependant, nous recommandons vivement d'acheter vos billets  l'avance pour garantir votre entre au festival.</p>
            <h3>3. Y a-t-il des billets VIP disponibles ?</h3>
            <p className='question3'>Oui, nous proposons des billets VIP offrant divers avantages exclusifs, tels que des zones de vision privilgies, des salons VIP, et bien plus encore. Consultez notre site web pour plus de dtails sur les options VIP disponibles.</p>
           </div>

          <div class="box2">
            <h2 id='logements'>Logements</h2>
            <h3>1. Y a-t-il un camping ou un hotel sur place ?</h3>
            <p className='question1'>
              Oui, nous proposons un camping sur place pour les festivaliers qui souhaitent sjourner  proximit de l'vnement. Des emplacements de camping sont disponibles  l'achat sur notre site web. N'hsitez pas  consulter les partenaires avec qui nous sommes associs dans la rubrique "partenaires".
              <Link to="/partenaires" id="clickhere" target="_blank"> Cliquez ici.</Link>
            </p>
            <h3>2. Existe-t-il des options d'hbergement alternatives  proximit du festival ?</h3>
            <p className='question2'>Oui, il existe plusieurs options d'hbergement alternatives  proximit du site du festival, y compris des htels, des auberges de jeunesse et des locations de vacances. Nous recommandons de rserver votre hbergement  l'avance, car les disponibilits peuvent tre limites pendant le festival. Notre partenaire abritel pourra vous donner toutes les informations requises  vos demandes <Link to="/partenaires" id="clickhere" target="_blank"> Cliquez ici.</Link></p>
          </div>

          <div class="box3">
            <h2 id='progartistes'>Programmation et Artistes</h2>
            <h3>1. O puis-je trouver le programme complet du festival ?</h3>
            <p className='question1'>
              Le programme complet du festival, y compris les horaires de chaque artiste et les activits prvues, sont disponibles sur notre site web dans la rubrique "programmations". Assurez-vous de consulter rgulirement notre site pour les mises  jour et les annonces importantes.
              <Link to="/programmations" id="clickhere" target="_blank"> Cliquez ici.</Link>
              Si vous souhaitez plus d'informations sur leurs prochaines tournes, vous pouvez consulter leurs prochaines dates dans notre sous-rubrique "Tournes 2025" ou<Link to="/artistes" id="clickhere" target="_blank"> Cliquez ici.</Link>
            </p>
            <h3>2. Qui sont les artistes confirms pour cette anne ?</h3>
            <p className='question2'>Vous pouvez consulter notre rubrique "programmations" pour retrouver tous les artistes du festival <Link to="/programmations" id="clickhere" target="_blank"> Cliquez ici.</Link></p>
           </div>
           </div>
           </div>

         <div className='formcontainer'>
          <h2 id='titleform'>FORMULAIRE DE CONTACT</h2>
          <form onSubmit={handleSubmit}> 
            <div>
            <label htmlFor="firstName">Prnom :</label>
            <input
              type="text"
              id="firstName"
              name="firstName"
              placeholder='Entrez votre prnom'
              value={formData.firstName} /* affiche la valeur correcte */ 
              onChange={handleChange} /* la fonction est appele  chaque fois que la valeur du formulaire change*/
              required
            />
           </div>
          <div>
            <label htmlFor="lastName">Nom :</label>
            <input
              type="text"
              id="lastName"
              name="lastName"
              placeholder='Entrez votre nom'
              value={formData.lastName}/* affiche la valeur correcte */ 
              onChange={handleChange}/* la fonction est appele  chaque fois que la valeur du formulaire change*/
              required
            />
           </div>
          <div>
            <label htmlFor="email">Email :</label>
            <input
              type="email"
              id="email"
              name="email"
              placeholder='Entrez votre email'
              value={formData.email}/* affiche la valeur correcte */ 
              onChange={handleChange}/* la fonction est appele  chaque fois que la valeur du formulaire change*/
            />
           </div>
          <div>
            <label htmlFor="message">Message :</label>
            <textarea
              id="message"
              name="message"
              placeholder='Entrez votre message'
              value={formData.message}/* affiche la valeur correcte */ 
              onChange={handleChange} /* la fonction est appele  chaque fois que la valeur du formulaire change*/
            ></textarea>
            </div>
          <button className="submit" type="submit">Envoyer</button>
          <button className='reset'type="reset"onClick={reset}>Reinitialiser</button>
                {/* La fonction est appele et met  jour la cl de rinitialisation du carrousel  chaque clic */}


           </form>

        {isSubmitted && ( // mise  jour de l'tat de donne du formulaire et envoi du message p
          <p id='oksubmit'>Merci ! Votre demande a t soumis avec succs :).</p>
        )}
           </div>
    </>
  );
  }