import React, { useEffect, useState } from 'react'; // Importer useState et useEffect depuis React
import "../../css/programmations.css";

const API_URL = '/sonosound/wp-json/wp/v2/posts?per_page=100';  // appel de l'API rest via change de donne fichier json de wordpress avec au plus de 50 articles dclars  dans wordpress

const Artistes = () => {
  const [artistes, setArtistes] = useState([]); // Etat pour la rcupration des donnes artistes via la mthode fetch
  const [rechercheArtiste, setRechercheArtiste] = useState(''); // Etat Recherche d'artiste (stock)
  const [rechercheScene, setRechercheScene] = useState(''); // Etat Recherche de scne (stock)
  const [rechercheDate, setRechercheDate] = useState(''); // Etat Recherche de date (stock)
  const [rechercheLieu, setRechercheLieu] = useState(''); // Etat Recherche de lieu (stock)
  const [aucuneDateTrouvee, setAucuneDateTrouvee] = useState(false); // tat pour indiquer si aucune date n'est trouve (stock)
  const [aucunArtisteTrouve, setAucunArtisteTrouve] = useState(false); // tat pour indiquer si aucun artiste n'est trouv (stock)
  const [aucuneSceneTrouve, setAucuneSceneTrouve] = useState(false); // tat pour indiquer si aucun artiste n'est trouv (stock)

  // Dclaration des IDs  afficher et filtrer depuis l'API wp 
  const filteredIds = [227, 190, 196, 195, 202, 209, 213, 218, 221, 224, 230, 234, 237, 240, 599, 603, 608, 611, 621, 627, 630, 633, 639, 642, 646, 653, 657, 661, 664, 667]; // fonction filtrage des ID => articles intgrs dans wordpress
  console.log(`retour id wp data concerts:`, filteredIds);

  useEffect(() => {
    const fetchArtistes = async () => {// utilisation de fetch pour effectuer une requte asynchrone des donnes API
      try {
        const response = await fetch(API_URL); // rcupration de l'url : '/sonosound/wp-json/wp/v2/posts?per_page=50
        if (!response.ok) {// vrifie la condition si response.ok est false si c'est le cas elle sera true, inversement de la logique.
          throw new Error("La rponse du serveur n'est pas OK");
        }
        const data = await response.json();
        const filteredData = data.filter(item => filteredIds.includes(item.id)); // filtre data par item / ID
        const updatedData = filteredData.map(item => ({
          ...item,
          title: { rendered: item.title.rendered.replace(/&#8211;/g, '-') }// filtre par titre par item du fichier json wordpress wp-json
        }));
        setArtistes(updatedData);
      } catch (error) {
        console.error('erreur rcupration donnes:', error);
      }
    };

    fetchArtistes();
  }, []);

  const rechercherArtistes = (event) => { // Fonction pour rechercher des artistes
    console.log('Filtre artiste :', event.target.value); // Affiche la valeur entre dans le champ de recherche d'artiste
    setRechercheArtiste(event.target.value); // Met  jour l'tat de recherche d'artiste avec la nouvelle valeur
};

  const rechercherScenes = (event) => {// Fonction pour rechercher par scne
    console.log('filtre scene ok', event.target.value);// Affiche la valeur entre dans le champ de recherche de scne
    setRechercheScene(event.target.value);// Met  jour l'tat de recherche de scne avec la nouvelle valeur
  };

  const rechercherDate = (event) => { // Fonction pour rechercher par date
    console.log('Filtre date :', event.target.value); // Affiche la valeur entre dans le champ de recherche de date
    setRechercheDate(event.target.value); // Met  jour l'tat de recherche de date avec la nouvelle valeur
};

  const rechercherLieu = (lieu) => {// Fonction pour rechercher par lieu
    console.log('filtre lieu  ok');// Affiche la valeur entre lors du l'evenement on click
    setRechercheLieu(lieu);// Met  jour l'tat de recherche de lieu avec la nouvelle valeur
  };


  const reinitialiser = () => {    // Fonction pour rinitialiser  des artistes, dates, lieux scnes.
    setRechercheArtiste('');//  met  jour l'tatde recherche d'artistes avec l'initialisation
    setRechercheDate('');//  met  jour l'tatde recherchede date avec l'initialisation
    setRechercheLieu('')//  met  jour l'tatde recherche de lieu avec l'initialisation
    setRechercheScene('')//  met  jour l'tatde recherche de scne avec l'initialisation
    console.log('reset ok');
  };

// Filtrage des artistes en fonction des critres de recherche
const filteredArtistes = artistes.filter(a => { // Utilisation de la mthode filter() sur le tableau artistes
  // vrifie si le titre de l'artiste contient le terme de recherche pour le nom de l'artiste
  const nomInclus = a.title.rendered.toLowerCase().includes(rechercheArtiste.toLowerCase());
  
  // vrifie si le titre de l'artiste contient le terme de recherche pour la scne
  const sceneInclus = a.title.rendered.toLowerCase().includes(rechercheScene.toLowerCase());
  
  // vrifie si le contenu de l'artiste contient le terme de recherche pour la date
  const dateInclus = a.content.rendered.toLowerCase().includes(rechercheDate.toLowerCase());
  
  // vrifie si le lieu de l'artiste correspond au lieu recherch, ou si aucun lieu n'est spcifi dans la recherche
  const lieuInclus = rechercheLieu === '' || a.content.rendered.toLowerCase().includes(rechercheLieu.toLowerCase());
  
  // Afficher un message de dbogage dans la console
  console.log('retour map data');
  
  // Retourner true si tous les critres de filtrage sont ok pour cet artiste, sinon false
  return nomInclus && sceneInclus && dateInclus && lieuInclus;
});


  useEffect(() => {
    // Vrification, si aucun artiste n'est trouv aprs le filtrage bas sur la date
    setAucuneDateTrouvee(filteredArtistes.length === 0 && rechercheDate !== '');
    console.log('pas de date dans la base non ok')
    // Vrification, si aucun artiste n'est trouv aprs le filtrage bas sur le nom
    setAucunArtisteTrouve(filteredArtistes.length === 0 && rechercheArtiste !== '');
    console.log('pas dartiste dans la base non ok')
    // Vrification, si aucun artiste n'est trouv aprs le filtrage bas sur la scene
    setAucuneSceneTrouve(filteredArtistes.length === 0 && rechercheScene  !== '');
    console.log('pas de scene dans la base non ok')

  // Retourner true si tous les critres de filtrage sont ok pour cet artiste, sinon false
  }, [filteredArtistes, rechercheDate, rechercheArtiste, rechercheScene ]);

  return (
    <div className="artistes-container">
      <h1 className='progtitre'>CONCERTS FESTIVAL</h1>
      <input
        type="text"
        placeholder="Rechercher un artiste ou une heure"
        value={rechercheArtiste}// stock de l'info rentr par l'utilisateur
        onChange={rechercherArtistes}// Appel de la fonction rechercherArtistes pour mettre  jour l'tat de recherche d'artiste(stock)
      />
      <input
        type="text"
        placeholder="Rechercher une Scene"
        value={rechercheScene}// stock de l'info rentr par l'utilisateur
        onChange={rechercherScenes}// Appel de la fonction rechercherArtistes pour mettre  jour l'tat de recherche de scnes (stock)
      />
      <input
        type="text"
        placeholder="Rechercher une date"
        value={rechercheDate}// stock de l'info rentr par l'utilisateur
        onChange={rechercherDate}// Appel de la fonction rechercherArtistes pour mettre  jour l'tat de recherche de date (stock)
        />
      

      <div className='lieubuttoncontainer'>
        <button className='lieu' onClick={() => rechercherLieu('Hotel de Ville')}>Htel de Ville</button>
        {/* Bouton de filtre, appel de la fonction rechercher lieu qui met  jour l'tat de recherche lieu avec l'venement on click (stock) */}
        
        <button className='lieu'onClick={() => rechercherLieu('Parc de Saint-Cloud')}>Parc de Saint-Cloud</button>
                {/* Bouton de filtre, appel de la fonction rechercher lieu qui met  jour l'tat de recherche lieu avec l'venement on click (stock) */}

        <button className='lieu'onClick={() => rechercherLieu('Bois de Boulogne')}>Bois de Boulogne</button>
                {/* Bouton de filtre, appel de la fonction rechercher lieu qui met  jour l'tat de recherche lieu avec l'venement on click (stock) */}

        <button className='lieu'onClick={() => rechercherLieu('Parc Saint Germain')}>Parc Saint Germain</button>
                {/* Bouton de filtre, appel de la fonction rechercher lieu qui met  jour l'tat de recherche lieu avec l'venement on click (stock) */}

        <button className='lieu'onClick={() => rechercherLieu('Golf de Saint Germain')}>Golf de Saint Germain</button>
                {/* Bouton de filtre, appel de la fonction rechercher lieu qui met  jour l'tat de recherche lieu avec l'venement on click (stock) */}

        <button className='reset'onClick={reinitialiser}>Rinitialiser tout</button>
        {/* La fonction est appele et met  jour la cl de rinitialisation du carrousel  chaque clic */}

      </div>

      <div className="artistes-grid">
        {aucuneDateTrouvee && <div className='findprog'>Aucune date trouve :(</div>} 
        {aucunArtisteTrouve && <div className='findprog'>Aucun artiste trouv :(</div>}
        {aucuneSceneTrouve && <div className='findprog'>Aucune Scne trouve :(</div>}
        {/* Filtre les artistes dont le contenu inclut la valeur de recherche (rechercheDate , recherche scene, recherche artiste)
        Si aucuneDateTrouvee ou aucunArtisteTrouve etc est vrai, aucun filtrage n'est appliqu.
        */}
        {!aucuneDateTrouvee && !aucunArtisteTrouve && !aucuneSceneTrouve && filteredArtistes.map((a, index) => (// map d ficher json pour rcuprer les donnes du fichier json wordpress pour chaque id filtr (id & titr & contenu)
         <a key={a.id} href='/artistes' className="boxlarge">
         <section>
           <h2>{a.title.rendered}</h2>
           <div dangerouslySetInnerHTML={{ __html: a.content.rendered }} />
         </section>
       </a>
        ))}
      </div>
    </div>
  );
};

export default Artistes;

