import React, { useEffect, useState } from 'react';
import "../../css/informations.css";

const API_URL = '/sonosound/wp-json/wp/v2/posts?per_page=100'; // appel de l'API rest via change de donne fichier json de wordpress avec au plus de 50 articles dclars  dans wordpress

const Informations = () => {// Component Sous section Informations du logo Accueil , Dropdown Menu Desktop => chemin liaison Menu.part.jsx + dropdownpart.jsx


  const [informations, setInformations] = useState([]); // Etat pour la rcupration des donnes infos viala mthode fetch (stock)
  const [rechercheInfos, setRechercheInfos] = useState('');//  Etat pour la recherche d'infos (stock)
  const [categorieFiltree] = useState(null); //  Etat pour le filtre d'infos (stock)
  const [aucuneInfosTrouvees, setAucuneInfosTrouvees] = useState(false); // tat pour indiquer si aucune info n'est trouve (stock)
  
  // Dclaration des IDs  afficher et filtrer depuis l'API wp
  const filteredIds = [452, 496, 522, 526, 536, 554]; // fonction filtrage des ID => articles intgrs dans wordpress
  console.log(`retour id wp data informations:`, filteredIds);

  useEffect(() => {
    const fetchInfos = async () => { // utilisation de fetch pour effectuer une requte asynchrone des donnes API wp
      try {
        const response = await fetch(API_URL); // rcupration de l'url : '/sonosound/wp-json/wp/v2/posts?per_page=50
        if (!response.ok) {// vrifie la condition si response.ok est false si c'est le cas elle sera true, inversement de la logique.
          throw new Error("La rponse du serveur n'est pas OK");
        }
        const data = await response.json();
        // Normalisation des donnes
        const updatedData = data.map(item => ({// filtre data par item / ID
          ...item,
          title: item.title.rendered.replace(/&#8211;/g, '-'),// filtre par titre par item du fichier json wordpress wp-json
          content: item.content.rendered, // filtre par contenu par item du fichier json wordpress wp-json
        }));

        // Tri des donnes en fonction des IDs spcifiques : [452, 496, 522, 526, 536, 554]
        const sortedData = updatedData.sort((a, b) => {
          return filteredIds.indexOf(a.id) - filteredIds.indexOf(b.id);
        });

        setInformations(sortedData);
      } catch (error) {
        console.error("erreur rcupration donnes:", error);
      }
    };

    fetchInfos();
  }, []);

  const rechercherInfos = event => { // Fonction pour rechercher des infos
    setRechercheInfos(event.target.value);// Met  jour l'tat de recherche d'infos avec la nouvelle valeur
  };

  // Filtrage des infos en fonction des critres de recherche

  const filteredInfos = informations.filter(info => {
    const infosInclus =
      // vrifie si le contenu de l'info contient le terme de recherche pour le nom de l'info
      info.content.toLowerCase().includes(rechercheInfos.toLowerCase()) ||
      // vrifie si le titre de l'info contient le terme de recherche pour le nom de l'info
      info.title.toLowerCase().includes(rechercheInfos.toLowerCase());
    const idInclus = filteredIds.includes(info.id);
    const categorieInclus = !categorieFiltree || info.categorie === categorieFiltree;

    return infosInclus && idInclus && categorieInclus;
  });

  useEffect(() => {
    // Vrifier si aucune information n'a t trouve aprs le filtrage
    setAucuneInfosTrouvees(filteredInfos.length === 0 && rechercheInfos !== '');
  }, [informations, rechercheInfos, categorieFiltree]);

  return (
    <div className="artistes-container1">
      <div className="artistecotainerchild1">
      <h1 className="titreinfos">LE DROULEMENT DU FESTIVAL</h1>
      <div className='catbuttoncontainer'>
      </div>
      <input
        type="text"
        placeholder="Rechercher une information"
        value={rechercheInfos} // stock de l'info rentr par l'utilisateur
        onChange={rechercherInfos}// Appel de la fonction rechercherInfos pour mettre  jour l'tat de recherche d'infos(stock)
      />

      {/* Afficher les informations filtres */}
      <div className="artistes-grid1">
        {aucuneInfosTrouvees && (// stock de l'info rentr par l'utilisateur mais fausse,  Si aucuneInfoTrouvee  est vrai, aucun filtrage n'est appliqu.
          <div className="datefindprog">Aucune information trouve :(</div>
        )}
        {!aucuneInfosTrouvees &&
          filteredInfos.map(info => (// map d ficher json pour rcuprer les donnes du fichier json wordpress pour chaque id filtr (id & titr & contenu)
            <div className={`boxlargeinfos`} key={info.id}>
              <a href={info.link} className="partner-link">
                <div>
                  <h2 className="title">{info.title}</h2>
                  <div dangerouslySetInnerHTML={{ __html: info.content }} />
                </div>
              </a>
            </div>
          ))}
      </div>
    </div></div>
  );
};

export default Informations;
