import React, { useEffect, useState } from 'react';
import "../../css/partenaires.css";

const API_URL = '/sonosound/wp-json/wp/v2/posts?per_page=100'; // appel de l'API rest via change de donne fichier json de wordpress avec au plus de 50 articles dclars  dans wordpress

const Partenaires = () => {
  const [categories, setCategories] = useState([]);// Etat pour la rcupration des donnes catgories  via la mthode fetch
  const [rechercheCategorie, setRechercheCategorie] = useState('');//  Etat pour la recherche de catgories (stock)
  const [aucunecategoriesTrouvees, setAucunecategoriesTrouvees] = useState(false); //  tat pour indiquer si aucune catgorie n'est trouve, affiche le message false

  const filteredIds = [370, 375, 392, 395, 398, 412, 417, 425, 431, 434, 435, 440, 443, 540, 545, 550]; // fonction filtrage des ID => articles intgrs dans wordpress
  console.log(`retour id wp partenaires:`, filteredIds);

  useEffect(() => {
    const fetchCategories = async () => {// utilisation de fetch pour effectuer une requte asynchrone des donnes API
      try {
        const response = await fetch(API_URL);// rcupration de l'url : '/sonosound/wp-json/wp/v2/posts?per_page=50
        if (!response.ok) {// vrifie la condition si response.ok est false si c'est le cas elle sera true, inversement de la logique.
          throw new Error('La rponse du serveur n\'est pas OK');
        }
        const data = await response.json();// appel et attente extraction des donnes en format json avec await
       // filtre data par item / ID
        const updatedData = data.map(item => ({
          ...item,
          title: { rendered: item.title.rendered.replace(/&#8211;/g, '-') }// filtre par titre par item du fichier json wordpress wp-json
        }));
        setCategories(updatedData);
      } catch (error) {
        console.error('erreur rcupration donnes:', error);
      }
    };

    fetchCategories();
  }, []);

  const Recherchercategorie = (categorie) => {// Fonction pour rechercher des catgories
    console.log('Filtre catgorie :', categorie); // Affiche la valeur entre dans le champ de recherche d'artiste
    setRechercheCategorie(categorie);// Met  jour l'tat de recherche de catgories avec la nouvelle valeur
  };


  const resetcategorie = () => {// Fonction pour rinitialiser des catgories
    setRechercheCategorie('');// Met  jour l'tat de recherche de catgories avec la nouvelle valeur
  };

// Filtrage des artistes en fonction des critres de recherche
  const filtrercategories = categories.filter(categorie => {
      // vrifie si le contenu de la catgorie contient le terme de recherche pour la catgorie
    const categorieInclus = categorie.content.rendered.toLowerCase().includes(rechercheCategorie.toLowerCase());
    const idInclus = filteredIds.includes(categorie.id); 
    return categorieInclus && idInclus;
  });

  useEffect(() => {
    // Vrification, si aucune catgorie n'est trouv aprs le filtrage bas sur les catgories
    setAucunecategoriesTrouvees(filtrercategories.length === 0 && rechercheCategorie !== '');
  }, [filtrercategories, rechercheCategorie]);

  return (
    <div className="partner-container">
      <h1 className="titrepartenaires">NOS PARTENAIRES</h1>

      {/* Category buttons */}
      <div className='catbuttoncontainer'>
        <button className='categorie' onClick={() => Recherchercategorie('Food')}>Food</button>
        {/* Bouton de filtre, appel de la fonction Recherchercategorie qui met  jour l'tat de recherche catgorie avec l'venement on click (stock) */}

        <button className='categorie' onClick={() => Recherchercategorie('Collectivite')}>Collectivite</button>
        {/* Bouton de filtre, appel de la fonction Recherchercategorie qui met  jour l'tat de recherche catgorie avec l'venement on click (stock) */}

        <button className='categorie' onClick={() => Recherchercategorie('Transports')}>Transports</button>
        {/* Bouton de filtre, appel de la fonction Recherchercategorie qui met  jour l'tat de recherche catgorie avec l'venement on click (stock) */}

        <button className='categorie' onClick={() => Recherchercategorie('Media')}>Media</button>
        {/* Bouton de filtre, appel de la fonction Recherchercategorie qui met  jour l'tat de recherche catgorie avec l'venement on click (stock) */}

        <button className='categorie' onClick={() => Recherchercategorie('Tourisme')}>Tourisme</button>
        {/* Bouton de filtre, appel de la fonction Recherchercategorie qui met  jour l'tat de recherche catgorie avec l'venement on click (stock) */}

        <button className='categorie' onClick={() => Recherchercategorie('Scurit')}>Scurit</button>
        {/* Bouton de filtre, appel de la fonction Recherchercategorie qui met  jour l'tat de recherche catgorie avec l'venement on click (stock) */}

        <button className='categorie' onClick={() => Recherchercategorie('Environnement')}>Environnement</button>
        {/* Bouton de filtre, appel de la fonction Recherchercategorie qui met  jour l'tat de recherche catgorie avec l'venement on click (stock) */}



        <button className='reset2' onClick={resetcategorie}>Rinitialiser les catgories</button>
      {/* La fonction est appele et met  jour la cl de rinitialisation du carrousel  chaque clic */}
      </div>

      <div className="artistes-grid">
        {/* Filtre les catgories dont le contenu inclut la valeur de recherche (rechercheCategorie)
        Si aucuneCategorieTrouvee  est vrai, aucun filtrage n'est appliqu.
        */} {aucunecategoriesTrouvees && <div className='catfindprog'>Aucune catgorie trouve :(</div>}
        {!aucunecategoriesTrouvees && filtrercategories.map(category => (// map d ficher json pour rcuprer les donnes du fichier json wordpress pour chaque id filtr (id & titr & contenu)
          <div className={`boxlargepart`} key={category.id}>
            <a href={category.link} className="partner-link">
              <div>
                <h2 className='title'>{category.title.rendered}</h2>
                <div dangerouslySetInnerHTML={{ __html: category.content.rendered }} />
              </div>
            </a>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Partenaires;
